.event {
	display: flex;
	align-items: center;
	justify-items: center;
	flex-direction: column;
	padding: 20px 20px 45px; /* Adjusted padding */
	margin-bottom: 5px; /* Adjusted margin */
  border: 1px solid #ccc;
  border-radius: 10px;
  position: relative;

}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

.btnSubmit {
	display: inline-block;
	padding: 9px 10px;
	font-size: 16px;
	font-weight: bold;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	border: 1px solid #4caf50; 
	color: #4caf50;
	background-color: #ffffff; 
	border-radius: 5px;
	transition: background-color 0.3s, color 0.3s, border-color 0.3s;
}
.btnError{display: inline-block;
	padding: 9px 10px;
	font-size: 16px;
	font-weight: bold;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	border: 1px solid #d4da3a; 
	color: #ffffff;
	background-color: #000000; 
	border-radius: 5px;
	transition: background-color 0.3s, color 0.3s, border-color 0.3s;}

.btnSubmit:hover {
	background-color: #4caf50; 
	color: #ffffff;
	border-color: #ffffff; 
}

.error-message {
	color: #ba3939;
  background: #ffe0e0;
  border: 1px solid #a33a3a;

}

.alerts-container {
  position: fixed;
  top: 0px;
  left: 20px;
  width: 250px;
  z-index: 1000;
}

.charts-container {
  display:grid;
	grid-template-columns: 1fr 1fr
}

@media (max-width: 768px){
	.charts-container{
			grid-template-columns: 1fr
	}
}


/* CSS Loader*/

.loader-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Adjust this based on your layout */
}

.loader {
	width: 48px;
	height: 48px;
	display: inline-block;
	position: relative;
	border: 3px solid;
	border-color:#de3500 #0000 #000000 #0000;
	border-radius: 50%;
	box-sizing: border-box;
	animation: 1s rotate linear infinite;
}
.loader:before , .loader:after{
	content: '';
	top: 0;
	left: 0;
	position: absolute;
	border: 10px solid transparent;
	border-bottom-color:#000000;
	transform: translate(-10px, 19px) rotate(-35deg);
}
.loader:after {
	border-color: #de3500 #0000 #0000 #0000 ;
	transform: translate(32px, 3px) rotate(-35deg);
}
 @keyframes rotate {
	100%{    transform: rotate(360deg)}
}